

@supports (--css: variables) {
  .menu__megamenu {
    width: 100%;
    max-width: var(--marco);
  }
}

.menu {
  $m: &;
 	display: flex;
  margin: auto;
	margin-right: 0;
	height: 95px;
	&__link {
		height: 95px;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		text-decoration: none;
		color: #606060;
		font-size: 1.7rem;
		padding: 1rem 2rem;
		font-weight: 300;
		@include transicion;
		&--mail {
			background: #10B5AC;
			&:hover {
				background: #0ca9a0 !important;
			}
		}
	}
	&__item {
		&:hover {
			.menu__link {
				background: #E7454E;
  			color: blanco(1);
			}
			.menu__megamenu {
				opacity: 1;
  			visibility: visible;
			}
		}
		&:nth-child(7) {
      background: #E7454E;
			@include transicion;
			&:hover {
        #{$m}__link {
          background: rgba(204, 0, 51,.5);
        }
			}
		}
		// &.active {
		// 	background: $cuaternario;
		// 	#{$m}__link {
		// 		color: blanco(1);
		// 	}
		// }
	}
	&__megamenu {
		position: absolute;
		top: 100%;
		right: 0;
		left: 0;
		background: #4C5A81;
		width: 1280px;
		margin: auto;
		padding: 5rem;
		opacity: 0;
		visibility: hidden;
		@include transicion;
		transition-delay: 200ms;
		display: flex;
		align-items: center;
		&__icon {
			margin-right: 5rem;
			img {
				width: 140px;
  			display: block;
			}
		}
		&__title {
			font-size: 2.8rem;
  		color: blanco(1);
		}
		&__row {
			display: flex;
  		margin: 0 -3rem;
		}
		&__col {
			padding: 0 3rem;
		}
		&__link {
			display: block;
			font-size: 1.7rem;
			color: #F93549;
			text-decoration: none;
			padding: 1rem 0;
			font-weight: 300;
		}
	}
}