.header {
  @include posicion(fixed,0,0,null,0);
  background: blanco(1);
  height: #{$header}px;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: space-between;
	padding: 0 0 0 2rem;
	box-shadow: 0 0 5px 1px rgba(0,0,0,.1);
  @include vars() {
    height: var(--header);
  }
  &__container {
    height: var(--header);
    width: 100%;
    max-width: var(--marco-big);
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__item {
      &--logo {
        width: 172px;
        height: 50px;
      }
    }
  }
  &__item {
    @include break(lg-mw) {
      height: 100%;
    }
    &--logo {
      display: flex;
      align-items: center;
			text-decoration: none;
			margin-right: auto;
      img {
        display: block;
        width: 133px;
        margin-right: 3rem;
      }
    }
    &--user {
      @include break(xxl-mw) {
        margin-left: auto;
        margin-right: 0;
      }
    }
    &--burger {
      margin-left: 1.5rem;
      svg {
        fill: $primario;
      }
    }
    &--hazte {
      a {
        text-decoration: none;
        height: 100%;
        background: #aec2cc;
        padding: 20px;
        display: block;
        @include break(md-mw) {
          padding: 15px;
        }
      }
    }
    &--acceso {
      a {
        text-decoration: none;
        height: 100%;
        background: #c03;
        padding: 20px;
        display: block;
        @include break(md-mw) {
          padding: 15px;
        }
      }
      img {
        width: 34px;
      }
    }
  }
  &__text {
    font-size: 3.2rem;
    font-style: italic;
    font-weight: 600;
    color: blanco(1);
    span {
      font-weight: 300;
    }
  }
  &__link {
    margin-right: 12px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__fonos {
    display: flex;
    align-items: center;
    padding-right: 20px;
    a {
      text-decoration: none;
      font-size: 1.5em;
      color: $primario;
      opacity: 1;
      @include transicion;
      &:hover {
        opacity: .7;
      }
    }
    span {
      font-size: 1.8em;
      margin: 0 10px;
      position: relative;
      top: -1px;
      color: $primario;
    }
  }
}

.header-user {
	display: flex;
	align-items: center;
  justify-content: space-between;
  background: $cuaternario;
  @include transicion;
  height: 100%;
  &:hover {
    background: rgba(204, 0, 51,.8);
  }
  
  &--acceso-clientes {
    img {
      width: 22px;
      margin-right: 10px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 1.6rem;
    color: blanco(1);
    padding: 5px 20px;
    @include transicion;
    background: #AEC2CC;
    height: 100%;
    width: 100%;
    span {
      padding-left: 10px;
    }
    &:hover {
      background: #a2bcca;
    }
    &.hazte-cliente {
      background: #AEC2CC;
      &:hover {
        background: #c2d3dc;
      }
    }
    &.acceso-clientes {
      background: #CC0033;
      &:hover {
        background: #e0194b;
      }
    }
  }
}
