.pasos {
  &__cabecera {
    margin: 60px 0;
    display: flex;
    align-items: center;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
  }
  &__previa {
    margin-bottom: 65px;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
    h4 {
      margin: 0;
      font-size: 2em;
      margin-bottom: 10px;
      color: $primario;
    }
    p {
      font-size: 1.6em;
      font-weight: 300;
      color: $primario;
    }
  }
  &__icon {
    margin-right: 15px;
    img {
      @include break(sm-mw) {
        width: 70px;
      }
      @media (max-width:365px) {
        width: 60px;
      }
    }
  }
  &__titulo {
    h1 {
      font-size: 3.6em;
      font-weight: 300;
      margin: 0;
      color: $primario;
      @include break(sm-mw) {
        font-size: 3.2em;
      }
      @media (max-width:365px) {
        font-size: 2.8em;
      }
      span {
        font-weight: 400;
        display: block;
      }
    }
  }
  &__indicador {
    margin-bottom: 30px;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
    p {
      font-size: 1.7em;
      font-weight: 400;
      color: $primario;
      text-align: center;
      span {
        &.doble-linea {
          font-weight: 300;
          @include break(md-mw) {
            display: block;
          }
        }
      }
    }
  }
  .numbers {
    position: relative;
    margin-bottom: 40px;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
    @include break(md-mw) {
      margin-bottom: 0;
    }
    &__movil {
      display: none;
      @include break(md-mw) {
        display: flex;
        justify-content: center;
        margin-bottom: 40px;
      }
    }
    &__numbers {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include break(md-mw) {
        display: none;
      }
    }
    &__item {
      font-size: 2.8em;
      width: 60px;
      height: 60px;
      background: #F3F6F8;
      color: $primario;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: relative;
      z-index: 1;
      &.activo {
        color: blanco(1);
        background: #ec6300;
      }
    }
    &__linea {
      position: absolute;
      background: #e6e6e6;
      width: 95%;
      height: 2px;
      top: 50%;
      left: 20px;
      @include break(md-mw) {
        display: none;
      }
    }
  }
  &__subtitle {
    color: $secundario;
    font-size: 2.4em;
    margin: 0;
    margin-bottom: 30px;
    text-align: center;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
    &--final {
      color: $primario;
      text-align: left;
      margin-bottom: 40px;
    }
  }
  &__resubtitle {
    font-size: 2.4em;
    margin: 0;
    margin-bottom: 30px;
    color: $secundario;
    transition: .3s linear;
    opacity: 0;
    text-align: center;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
  }
  &__form {
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
  }
  .form {
    margin-bottom: 45px;
    &__subtitle {
      font-size: 2em;
      color: $primario;
      margin: 40px 0 20px;
    }
    &__user-title {
      font-size: 1.7em;
      color: $primario;
      margin-bottom: 25px;
    }
    &__tooltip {
      width: 22px;
      height: 22px;
      background: #A6ADB2;
      font-size: 0.8em;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      color: #fff;
      margin-left: 10px;
    }
    &__grupo[data-animacion=placeholder] {
      margin-bottom: 2.5em;
      label {
        &.refocus {
          opacity: 1;
          transform: translate(0,0) scale(0.85);
        }
      }
    }
    &__grupo {
      &--pass {
        position: relative;
        .show-pass {
          position: absolute;
          z-index: 11;
          top: 55%;
          right: 15px;
          font-size: 1.8em;
        }
      }
      &--select-span {
        margin-bottom: 1em !important;
        @include break(lg-mw) {
          margin-bottom: 2em !important;
        }
      }
      &--hidden {
        display: none;
      }
    }
    &__box {
      &--checks {
        min-height: 55px;
        margin-bottom: 20px;
      }
    }
    &__checkbox {
      display: inline-flex;
      align-items: baseline;
      @include break(sm-mw) {
        flex-direction: column;
        width: 80%;
      }
      .check-style {
        top: 6px;
        @include break(sm-mw) {
          top: 25px;
        }
      }
      label {
        margin-left: 15px;
        font-size: 1.7em;
        font-weight: 300;
        @include break(md-mw) {
          padding-right: 20px;
        }
        @include break(sm-mw) {
          padding-left: 15px;
          margin-right: 10px;
        }
      }
      &__item {
        top: 5px;
        border: 1px solid #A6ADB2;
        border-radius: 0;
        @include break(sm-mw) {
          top: 24px;
        }
      }
      &--long {
        input {
          top: 7px;
          @include break(sm-mw) {
            top: 24px;
          }
        }
      }
      &--checked {
        .form__checkbox__item {
          background: blanco(1);
          &:before {
            color: $secundario;
            border: 1px solid $secundario;
            background: blanco(1);
          }
        }
        // label {
        //   color: $secundario !important;
        // }
      }
      .long-check {
        width: 22px;
        @media (max-width:1060px) {
          width: 28px
        }
        @include break(lg-mw) {
          width: 18px;
        }
        &:before {
         right: -2px;
       }
      }
    }
    .text, select {
      background: blanco(1);
      font-size: 1.6rem;
      color: #333;
      padding: 12px 10px;
      border: 0;
      border-bottom: 1px solid #ccc;
      border-radius: 0;
      &.invalido {
        border-color: #ea5233;
      }
      &.disabled {
        background: #e7e7e7;
        border-color: #e7e7e7;
      }
    }
    label {
      color: #333;
      opacity: 1;
    }
    &__adicional,
    &__adicional-dos {
      color: #666;
      font-size: 1.4em;
      font-weight: 300;
      position: absolute;
      bottom: 0;
      z-index: 1;
      &.hide {
        visibility: hidden;
      }
    }
    &__adicional {
      &--paso2 {
        bottom: 5px;
        position: relative;
        @include break(lg-mw) {
          display: flex;
          margin-bottom: 20px;
          margin-top: 5px;
          bottom: 0;
        }
      }
    }
    &__adicional-dos {
      position: relative;
      bottom: 20px;
    }
    &__buttons {
      margin-top: 60px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include break(sm-mw) {
        flex-direction: column-reverse;
      }
      &--one {
        justify-content: flex-end;
      }
      &--paso2 {
        margin: 40px 0;
      }
    }
    &__volver {
      color: #66747d;
      font-size: 1.7em;
      font-weight: 300;
      text-decoration: none;
      outline: 0;
      display: flex;
      align-items: center;
      @include transicion;
      @include break(sm-mw) {
        margin-top: 20px;
      }
      span {
        font-size: 1.6em;
        margin-right: 10px;
      }
      &:hover {
        color: $secundario;
      }
    }
    &__lightbox {
      // position: absolute;
      // right: -25px;
      // top: 2px;
      font-size: 1.5em;
      z-index: 1;
      background: #a6adb2;
      color: white;
      display: inline-flex;
      width: 20px;
      height: 20px;
      font-weight: 600;
      align-items: center;
      border-radius: 50%;
      justify-content: center;
      text-decoration: none;
      outline: 0;
      margin-left: 5px;
      cursor: default;
      @include break(sm-mw) {
        position: absolute;
        top: 25px;
        right: 15px;
      }
    }
  }
  &__contenedor {
    margin-bottom: 10px;
    &--big {
      min-height: 110px;
    }
    &--small {
      margin-bottom: 65px;
    }
    @include break(md-mw) {
      margin-bottom: 30px;
    }
  }
  &__cedula {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    @include break(sm-mw) {
      flex-direction: column-reverse;
    }
    &--paso6 {
      align-items: center;
      margin-bottom: 15px;
    }
    p {
      color: $primario;
      font-size: 1.6em;
      font-weight: 300;
      padding-right: 20px;
      @include break(sm-mw) {
        display: block;
        margin-bottom: 10px;
      }
    }
  }
  .btn {
    &--neutro {
      color: #A6ADB2;
      font-size: 1.7em;
      font-weight: 300;
      text-transform: uppercase;
      &:hover {
        background: $secundario;
        border-color: $secundario;
        color: blanco(1);
      }
      &.invalido {
        border: 1px solid #a91e12;
      }
      &.loaded {
        background: #EC6300;
        color: blanco(1);
      }
    }
  }
  &__imgcedula {
    min-height: 235px;
    // height: 235px;
    @include break(md-mw) {
      min-height: auto;
    }
    &__interior {
      background: #f3f5f4;
      padding: 20px;
      min-height: 270px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      position: relative;
    }
    img {
      width: 300px;
      max-height: 230px;
      margin: auto;
      display: block;
      @include break(md-mw) {
        max-height: initial;
      }
    }
  }
  .pdf-view {
    display: none;
    .pdf-name {
      font-size: 1.6em;
      font-weight: 300;
      color: #333;
      padding: 0 20px;
      width: 100%;
      overflow: hidden;
      word-break: break-word;
      text-align: center;
    }
    .fln-pdf-light {
      font-size: 5em;
      color: #333;
    }
    &.vista-pdf {
      display: flex;
      width: 100%;
      height: 100%;
      position: absolute;
      background: #f3f5f4;
      align-items: center;
      justify-content: center;
      flex-direction: column-reverse;
    }
  }
  &__docs {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #F3F6F8;
    padding: 10px;
    margin-bottom: 20px;
    @include transicion;
    text-decoration: none;
    outline: 0;
    &:hover {
      background:blanco(1);
      border-color: $secundario;
      box-shadow: 0 5px 10px rgba(236,99,0,0.3);
      p, span {
        color: $secundario;
      }
    }
    &__interior {
      display: flex;
    }
    p {
      font-size: 1.7em;
      font-weight: 300;
      color: $primario;
    }
    &__check {
      color: $primario;
      font-size: 2.2em;
      margin-right: 15px;
    }
    &__pdf {
      color: $primario;
      font-size: 2.3em;
    }
  }
  &__qr {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: -36px;
    @include break(lg-mw) {
      top: 0;
    }
  }
  &__qr-info {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    &__izq {
      margin-right: 20px;
      p {
        font-size: 1.5em;
        color: $primario;
      }
      span {
        font-weight: 600;
      }
    }
  }
  &__tooltip {
    width: 22px;
    height: 22px;
    background: #A6ADB2;
    font-size: 0.8em;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: #fff;
    margin-left: 10px;
    outline: 0;
  }
}

.clonar {
  &__container-btn {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 20px;
  }
  &__btn {
    border: 1px solid #ec6300;
    padding: 10px 20px;
    color: #2A3A47;
    font-size: 1.7em;
    text-decoration: none;
    font-weight: 300;
    outline: 0;
    transition: all ease 300ms;
    &:hover {
      background:#ec6300;
      color: blanco(1);
    }
    @include break(sm-mw) {
      width: 100%;
      text-align: center;
    }
  }
  &__box {
    margin-bottom: 40px;
  }
}

.intro-tooltip {
  &--interior {
    position: absolute;
    right: -25px;
    top: 2px;
    font-size: 1.5em;
    z-index: 1;
    background: #A6ADB2;
    color: blanco(1);
    display: inline-flex;
    width: 20px;
    height: 20px;
    font-weight: 600;
    align-items: center;
    border-radius: 50%;
    justify-content: center;
    outline: 0;
    cursor: default;
    @include break(md-mw) {
      right: 0;
    }
    @include break(sm-mw) {
      right: 0;
      top: 23px;
    }
  }
}