.intro {
  position: relative;
  // background: url("../img/fondo-intro.jpg") no-repeat;
  background: url("/haztecliente-pj/img/fondo-intro.jpg") no-repeat;
  background-size: cover;
  background-position-x: center;
  padding: 100px 0;
  min-height: 690px;
  @include break(lg-mw) {
    padding: 60px 0;
  }
  &__item {
    background: blanco(1);
    padding: 35px;
    min-height: 420px;
    @media (max-width:1032px) {
      min-height: 471px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    @media (max-width:996px) {
      min-height: 505px;
    }
    @include break(lg-mw) {
      margin-bottom: 30px;
      min-height: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    &--blue {
      background: #2A3A47;
      transition: .3s linear;
      opacity: 0;
      &.observar--activo {
        opacity: 1;
      }
      @include break(lg-mw) {
        opacity: 1;
        margin-bottom: 0;
      }
      h2 {
        color: blanco(1);
        margin: 0;
        font-size: 2.8em;
        font-weight: 300;
        margin-bottom: 30px;
        @media (max-width:1304px) {
          margin-bottom: 25px;
        }
      }
      p {
        color: blanco(1);
        font-weight: 300;
        margin-bottom: 5px;
      }
      li {
        font-size: 1.7em;
        font-weight: 300;
        color: blanco(1);
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        &:last-child {
          margin-bottom: 0;
        }
        img {
          margin-right: 10px;
        }
      }
    }
  }
  &__header {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
  }
  &__logo {
    margin-right: 15px;
    img {
      @include break(sm-mw) {
        width: 80px;
      }
    }
  }
  &__title {
    h1 {
      font-size: 5em;
      margin: 0;
      font-weight: 300;
      color: $primario;
      line-height: 1.1;
      @include break(sm-mw) {
        font-size: 3.4em;
        line-height: 1;
      }
      span {
        font-weight: 400;
        display: block;
      }
    }
  }
  &__content {
    margin-bottom: 64px;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
      margin-bottom: 40px;
    }
    p {
      font-size: 2em;
      font-weight: 300;
      margin: 0;
      color: $primario;
      @include break(sm-mw) {
        font-size: 1.8em;
      }
    }
  }
  &__buttons {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
    @include break(sm-mw) {
      flex-direction: column;
    }
    .btn {
      width: 100%;
      margin-bottom: 5px;
    }
    p {
      color: $primario;
      font-size: 1.4em;
      font-weight: 300;
      text-align: center;
    }
    .intro-tooltip {
      display: inline-flex;
      width: 20px;
      height: 20px;
      font-weight: 600;
      align-items: center;
      border-radius: 50%;
      border: 1px solid $primario;
      justify-content: center;
      outline: 0;
      position: relative;
      top: 1px;
      font-size: 1.3em;
      cursor: default;
    }
  }
  &__btn {
    width: 48%;
    @include break(sm-mw) {
      width: 100%;
      margin-bottom: 30px;
    }
    &--juridica {
      @include break(sm-mw) {
        margin-bottom: 0;
      }
    }
  }
  &__rebuttons {
    display: flex;
    margin-top: 30px;
    align-items: center;
    justify-content: space-between;
    @media (max-width:1304px) {
      margin-top: 19px;
    }
    @include break(md-mw) {
      margin-top: 25px;
    }
    @include break(sm-mw) {
      flex-direction: column;
    }
  }
  &__borde-button {
    padding: 10px 25px;
    border: 2px solid blanco(1);
    text-decoration: none;
    color: blanco(1);
    font-size: 1.5em;
    @include transicion;
    width: 48%;
    text-align: center;

    @include break(sm-mw) {
      margin-bottom: 20px;
      width: 100%;
      text-align: center;
    }
    &:hover {
      background: blanco(1);
      color: $primario;
    }
    span {
      display: block;
    }
  }
}