$header: 90;
$marco: 1280;
$marcoBig: 1700;

:root {
  --header: #{$header}px;
  --marco: #{$marco}px;
  --marco-big: #{$marcoBig}px;
  @include break(md-mw) {
    --header: #{$header - 25}px
  }
}

body {
  padding-top: #{$header}px;
  @include vars() {
    padding-top: var(--header);
  }
}

.marco {
  max-width: #{$marco}px;
  margin: auto;
  @include vars() {
    max-width: var(--marco);
  }
}

.section {
  position: relative;
}

.btn-subir {
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #333;
	font-size: 3rem;
	color: blanco(1);
	cursor: pointer;
	position: fixed;
	right: 0;
	bottom: 100px;
	z-index: 9999;
	visibility: hidden;
	opacity: 0;
	@include transicion;
	&--activo {
		visibility: visible;
    opacity: 1;
	}
	@include break(sm-mw) {
		bottom: 130px;
	}
}

.tippy-tooltip {
  background: #A6ADB2;
  font-size: 1.4em;
  padding: 10px;
  border-radius: 10px;
  font-weight: 300;
}

.tippy-popper[x-placement^=bottom] .tippy-arrow {
  border-bottom-color: #A6ADB2;
}

.btn {
  border-radius: 0;
  margin-bottom: 5px;
  font-weight: 300;
  font-size: 1.7em;
  @include break(sm-mw) {
    width: 100%;
  }
  &--neutro {
    position: relative;
    input {
      cursor: pointer;
      position: absolute;
      top: -24px;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 4;
      opacity: 0;
      width: 100%;
    }
  }
}

.alerta {
  &--error {
    color: #a91e12;
    background: #f7b5af;
  }
}

.respuestas {
  &__item {
    min-height: 470px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    transition: .3s linear;
    opacity: 0;
    &.observar--activo {
      opacity: 1;
    }
    @include break(lg-mw) {
      opacity: 1;
    }
    @include break(md-mw) {
      margin-bottom: 40px;
    }
  }
  &__icon {
    text-align: center;
    span {
      font-size: 5em;
      color: #2a3a47;
    }
  }
  &__texto {
    text-align: center;
    margin-bottom: 30px;
    max-width: 700px;
    h2 {
      margin: 0;
      margin-bottom: 30px;
      color: #2a3a47;
    }
    p {
      font-weight: 300;
      color: #2a3a47;
      font-size: 1.7em;
    }
  }
  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

select#relacion-sartor {
  padding-right: 30px;
  text-overflow: ellipsis;
}

.modal {
  background: white;
	padding: 30px;
	text-align: left;
	max-width: 1100px;
	margin: 40px auto;
  position: relative;
  max-height: 600px;
  overflow-y: scroll;
  @include break(md-mw) {
    max-height: 550px;
  }
  &--home {
    max-height: initial;
    overflow-y: hidden;
    li {
      padding-left: 15px;
    }
  }
  h4 {
    font-size: 2em;
    margin: 0;
    margin-bottom: 20px;
    color: #243746;
  }
  p {
    font-size: 1.6em;
    font-weight: 300;
    margin-bottom: 15px;
    color: #2a3a47;
    &.modal__pregunta {
      font-weight: 400;
      margin-bottom: 10px;
    }
  }
  ul {
    list-style-type: none;
    margin-bottom: 10px;
    li {
      position: relative;
      color: #2a3a47;
      font-weight: 300;
      font-size: 1.6em;
      // padding-left: 15px;
      margin-bottom: 6px;
      &:before {
        content: '';
        position: absolute;
        width: 5px;
        height: 5px;
        background: #ec7700;
        top: 8px;
        left: 0;
      }
    }
  }
  &__lista {
    li {
      padding-left: 0;
    }
    &__lista {
      font-size: 1rem;
      margin-top: 15px;
    }
    &--uno {
      li {
        padding-left: 15px;
      }
    }
    &--dos,
    &--tres,
    &--cuatro {
      li {
        padding-left: 0;
        display: flex;
        &:before {
          display: none;
        }
      }
      span {
        color: #ec7700;
        font-weight: 500;
        margin-right: 5px;
      }
    }
    &--tres {
      li {
        display: block;
      }
      .modal__lista__lista {
        padding-left: 15px;
      }
    }
  }
  &__link {
    text-decoration: none;
    outline: 0;
    color: #EC6300;
    &:hover {
      text-decoration: underline;
    }
  }
}

.representante {
  margin-bottom: 20px;
  @include break(lg-mw) {
    margin: 10px 20px;
  }
  &.invalido {
    border: 1px solid #ea5233;
  }
  &--paso6 {
    padding: 20px 0;
    @include break(lg-mw) {
      margin: 10px 20px;
    }
  }
}

.cabecera {
  display: block;
  font-size: 1.7rem;
  font-weight: 300;
  padding: 25px;
  margin-bottom: 1px;
  color:#333333;
  cursor: pointer;
  // background: #f5f5f5;
  transition: all ease 200ms;
  transition-delay: 200ms;
  border-radius: 0;
  outline: 0;
}

.cabecera:hover .desplegable__editar {
  color: #EC6300;
}

.bloque_desplegable {
  margin-bottom: 10px;
  border: 1px solid #C5CACD;
  &.invalido {
    border: 1px solid #ea5233;
  }
}

.contenido.fila {
  padding: 0 25px 40px 25px;
}

.desplegable__cabecera .ui-icon {
  float: right;
  font-size: 18px;
  transition: all ease 200ms;
}

.desplegable__contenido {
  display: none;
  padding: 30px;
  background: #F0F4F4;
  /* box-shadow: 0 6px 12px rgba(51, 51, 51, 0.2); */
  position: relative;
  border-radius: 0;
  margin-bottom: 20px;
}

.desplegable__contenido p {
  font-size: 1.6rem;
  font-weight: 300;
  color: #333;
}

.desplegable__cabecera__interior {
  display: flex;
  align-items: center;
}

.desplegable__cabecera__texto {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.desplegable__cabecera__icons {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.desplegable__editar {
  font-size: 2.8rem;
}

.desplegable__borrar {
  text-decoration: none;
  outline: 0;
  color: #333;
  transition: all ease 300ms;
}

.desplegable__borrar:hover {
  color:#EC6300;
}

.desplegable__borrar span {
  font-size: 2.8rem;
}

.desplegable__cabecera__texto p {
  color: #333;
}

.contenido__borrar {
  text-align: right;
}

@media (max-width:767px) {
  .desplegable__cabecera__texto p {
    font-size: 1.4rem;
  }
}

@media (max-width:600px) {
  .desplegable__cabecera__texto {
    align-items: flex-start;
    flex-direction: column;
  }
  .desplegable__cabecera__texto p {
    font-size: 1.5rem;
  }
}

.error {
  display: block;
  position: relative;
  margin-top: 10px;
  padding: 10px;
  background: #efa3a3;
  font-size: 1.5em;
  color: #9c4242;
  text-align: center;
}