.footer {
  position: relative;
  background: #E3E3E3;
  z-index: 9999;
  &__top {
    padding: 6rem 1.5rem 3rem;
    @include break(lg-mw) {
      padding: 3rem 1.5rem;
    }
  }
  &__logo {
    display: flex;
    width: 172px;
    height: 50px;
    @include break(md-mw) {
      margin: auto;
      margin-bottom: 30px;
    }
  }
  &__fonos {
    text-align: right;
    @include break(md-mw) {
      text-align: center;
    }
    p {
      color: $primario;
    }
    &__interior {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      @include break(md-mw) {
        justify-content: center;
      }
      @include break(sm-mw) {
        flex-direction: column;
      }
      p, span {
        color: $primario;
      }
      p {
        margin-right: 10px;
        @include break(sm-mw) {
          margin-right: 0;
          margin-bottom: 5px;
        }
      }
      span {
        font-size: 1.6em;
        margin: 0 5px;
        position: relative;
        top: -1px;
        @include break(sm-mw) {
          display: none;
        }
      }
    }
  }
  &__call {
    font-size: 1.4em;
    text-decoration: none;
    color: $primario;
    @include transicion;
    @include break(sm-mw) {
      font-size: 1.5em;
      margin-bottom: 5px;
    }
    &:hover {
      opacity: .6;
    }
  }
  .footer__bottom {
    background: rgba(42, 58, 71, 0.2);
    padding: 1.5rem;
    &__legal {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width:845px) {
        flex-direction: column;
        text-align: center;
      }
    }
    p {
      color: #2A3A47;
      font-size: 1.4em;
      font-weight: 300;
      span {
        @media (max-width:415px) {
          display: block;
        }
      }
    }
  }
}
